/* src/Components/controlButtons/ControlButton.css */

/**===========================================================================
 *                          ControlButton Styling
 *===========================================================================**/

/* General styling for the control button body */
#ControlButton .body {
  margin: 4px;
  display: flex; /* Changed to flexbox for easier centering */
  justify-content: center; /* Centers the buttons horizontally */
  align-items: center; /* Centers the buttons vertically (if needed) */
}

/* Container styling for control buttons */
#ControlButton .control-buttons-container {
  width: 100%; /* Ensure container takes full width */
  width: 518px;
  display: flex;
  justify-content: center; /* Centers the buttons within the container */
  align-items: center;
  /* padding-bottom: 1rem; */
  /* gap: 10px;  */
  /* Adds space between the buttons */
}

/* Styling for individual control buttons */
/* width - auto - Allow buttons to size according to their content */
#ControlButton .control-button {
  /* width: auto;  */
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  background-color: #1a1a1a;
  color: #ffffff;
  border-radius: 8px;
  border: 1px solid transparent;
  cursor: pointer;
  transition: border-color 0.25s, background-color 0.25s;

  /* Hover state for buttons */
  &:hover {
    border-color: #646cff;
    background-color: #333333;
  }

  /* Focus state for buttons */
  &:focus,
  &:focus-visible {
    outline: 4px auto -webkit-focus-ring-color;
  }
}
