/* src/Components/Keypad/Keypad.css */

 /* Set max-width to control overall size */
#Keypad .keypad-container {
  width: 484px;
  max-width: 484px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;
  margin: 0 auto;
  margin-bottom: 3.25rem;
}

#Keypad .keypad-button {
  background-color: white;
  color: #0d4521;
  border-radius: 0.375rem;
  padding: 0.75rem;
  font-size: 1.5rem;
}

#Keypad .zero-button {
  grid-column: span 3;
}

