/* src/components/InningBox.css */

/* Centering the content of InningBox */
#InningBox .inning-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
  font-size: 1.875rem;
}


#InningBox .inning-container {
  background-color: #006400;
  max-width: 100px;
  border-radius: 0.375rem;
  color: white;
  text-align: center;
  font-size: 1.875rem;
  border: 2px inset rgb(255, 255, 255);
}

  
#InningBox
.inning-container, .inning-number, .arrow-box {
  font-size: 2.5rem;
}

#InningBox .inning-header {
  margin-top: 2rem;
  justify-content: center;
  font-size:xx-small;
  display: flex;
}

  

/**==============================================
 * *                InningBox 
 *   Inning Arrows stling
 *   
 *   
 *
 *=============================================**/
#InningBox
.inning-content {
  font-size: 60;
  font-stretch:wider;
  font-weight:600;
    padding-inline: 0.5rem;
    padding-block: 0.20rem;
    display: flex; 
    justify-content: center;
  }

  #InningBox
  .arrow-box {
    display:flex;
    margin-inline: 5px;
    margin-right: 10px;
    width: 40px;
    height: 30px;
    justify-content: center;
    align-items: center;
  }
  
  #InningBox
  .top-arrow,
  .bottom-arrow {
    font-size: 48px;
  }
  
  #InningBox
  .illuminated {
    font-size: 48px;
    color: white; 
  }
  
  #InningBox
  .not-illuminated {
    font-size: 48px;
    color: rgb(87, 85, 85);
  }
