/* src/Components/ScoreBox/ScoreBox.css */

/* Style for the container holding ScoreBox and InningBox elements */
.score-boxes-container {
  display: flex;
  justify-content: center; /* Center all elements in the container */
  align-items: center; /* Align items vertically in the center */
  padding: 0.5rem; /* Optional padding around the container */
  gap: 20px; /* Adjusts the spacing between the elements */
}

/* Styling for the ScoreBox elements */
/* Ensure the ScoreBox takes up to 30% of the container's width */
#ScoreBored .score-container {
  max-width: 100%; 
  position: relative;
  background-color: #064e3b;
  min-width: 180px;
  border: 2px solid black;
  color: white;
  text-align: center;
  margin-top: 1.25rem;
  /* padding: 1rem 1.25rem; */
  border-radius: 0.375rem;
  border: inset rgb(255, 255, 255);
  /* box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.1); */
  /* width: 8rem; */
}

/* Specific styling for the team name and score */
#ScoreBored .team-name {
  font-size: 1.25rem;
  font-weight: 700;
  padding-bottom: 4px;
}

#ScoreBored .score-total {
  font-size: 3rem;
  font-weight: 800;
}

/* Styling for the divider line */
#ScoreBored .divider {
  /* position: absolute; */
  height: 4px;
  background-color: white;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .score-boxes-container {
    width: 550px;
    flex-direction: column; /* Stack the ScoreBox and InningBox vertically on smaller screens */
    align-items: center; /* Ensure the elements are centered when stacked */
    gap: 10px; /* Reduce the gap between elements for smaller screens */
  }
  
  .score-container {
    max-width: 100%; /* Allow full width on smaller screens */
  }
}
